.App {
  text-align: center;
  /* background-color: white; */
}
.plan-card {
  width: 350px;
  padding-bottom: 30px;
  border: 1px solid #ccc;
  border-radius: 12px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
  background-color: white;
  /* margin-bottom: 20px; */
  /* height: 500px; */
}
.title {
  color: gray;
}
.subs-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin: 100px;
}
.disable-features {
  color: rgb(176, 176, 176);
}
.plan-spec {
  display: flex;
  text-align: left;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.button {
  background-color: rgb(7, 179, 248);
  color: white;
  border-style: none;
  width: 75%;
  border-radius: 20px;
  padding: 10px; 
  cursor: pointer;
  margin-top: 20px;
}
.button:hover {
  background-color: rgb(135, 217, 249); 
  
}
.disabled {
  background-color: rgb(7, 179, 248);
  color: white;
  border-style: none;
  width: 75%;
  border-radius: 20px;
  padding: 10px;
  margin-top: 20px;
  /* cursor: not-allowed; */
}